import React, { useState, useEffect } from 'react';
import './PeoplePage.scss';

export const PeoplePage = () => {
  const [people, setPeople] = useState();

  console.log(people);

  useEffect(() => {
    getPeople(setPeople);
  }, []);

  return (
    <>
      <h2>Peope page</h2>
      <ul>
        {people
          && people.map(person => {
            console.log(person);
            return <li key={person.name}>{person.name}</li>
          })}
      </ul>
    </>
  );
}

async function getPeople(callback) {
  const response = await fetch(`https://mate-academy.github.io/react_people-table/api/people.json`);

  const people = await response.json();

  callback(people);
 /*  callback(
    people.map(person => ({
      ...person,
      mother: people.find(mother => person.mother),
      father: people.find(father => person.father),
    }))) */
}